.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  text-decoration: none;
  color: #0d6efd;
}

a.header-link {
  margin: 1.5em;
  font-weight: bold;
}

.ChartsVersion {
  margin-top: 4em;
}

.chartOvlTitle {
  font-size: 1.15em;
  font-weight: bold;
  margin-top: 2em;
  margin-bottom: 0em;
  padding: 0;
};

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
